<template>
  <v-app>
    <v-toolbar
      app
      :clipped-left="clipped"
      dark
      class="bg-abhi-apri-color"
    >
      <v-toolbar-title><router-link class="nav-link-text-color" to="/"><img src="static/images/abhipri-logo.png" class="img-fluid"/></router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat><router-link class="nav-link-text-color" to="/">Home</router-link></v-btn>
        <v-btn flat><router-link class="nav-link-text-color" to="/apparel">Apparels</router-link></v-btn>
        <v-btn flat><router-link class="nav-link-text-color" to="/contact">Contact us</router-link></v-btn>
        <v-btn flat><router-link class="nav-link-text-color" to="/cart">Cart</router-link></v-btn>
        <v-btn flat><router-link class="nav-link-text-color" to="/login-signup">Login/Signup</router-link></v-btn>
      </v-toolbar-items>
      <v-btn class="hidden-md-and-up navbar-toggler" icon @click.stop="rightDrawer = !rightDrawer">
        <v-icon>menu</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <v-navigation-drawer
      temporary
      :right="right"
      v-model="rightDrawer"
      fixed
      app
    >
      <v-list>
        <v-list-tile class="nav-link-text-color">
          <v-list-tile-title><router-link class="nav-link-text-color" to="/">Home</router-link></v-list-tile-title>
        </v-list-tile>
        <v-list-tile class="nav-link-text-color">
          <v-list-tile-title><router-link class="nav-link-text-color" to="/apparel">Apparels</router-link></v-list-tile-title>
        </v-list-tile>
        <v-list-tile class="nav-link-text-color">
          <v-list-tile-title><router-link class="nav-link-text-color" to="/contact">Contact us</router-link></v-list-tile-title>
        </v-list-tile>
        <v-list-tile class="nav-link-text-color">
          <v-list-tile-title><router-link class="nav-link-text-color" to="/cart">Cart</router-link></v-list-tile-title>
        </v-list-tile>    
        <v-list-tile class="nav-link-text-color">
          <v-list-tile-title><router-link class="nav-link-text-color" to="/login-signup">Login/Signup</router-link></v-list-tile-title>
        </v-list-tile>                   
      </v-list>
    </v-navigation-drawer>
    <FooterBlock/>
    <div class="text-xs-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
          Privacy Policy
        </v-card-title>

        <v-card-text>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            flat
            @click="dialog = false"
          >
            I accept
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
      items: [{
        icon: 'bubble_chart',
        title: 'Inspire'
      }],
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: 'Vuetify.js',
      dialog: false
    }
  },
  name: 'App',
  components: {
    FooterBlock: () => import('@/components/Footer.vue')
  },
  mounted () {
    var self = this
    setTimeout(() => {
      self.dialog = true
    }, 6000)
  }
}
</script>
<style scoped>
.img-fluid {
  width: 12em;
}
.bg-abhi-apri-color,.v-navigation-drawer--temporary {
  background-color: #231f20 !important;
}
.navbar-toggler {
    background: #bf9e56 !important;
    color: rgba(0,0,0,.5);
    border-color: rgba(0,0,0,.1);
}
.nav-link-text-color {
    color: #ba9e56 !important;
    text-transform: uppercase;
    font-size: 15px;
    width: 12em;
    text-align: center;
    text-decoration: none;
}
</style>




